<template>
  <div id="contentList">
    <div v-if="contentShow" class="loading-content">
      <img src="../assets/picture/loading_green.c0f9be0.gif" alt=""/>
    </div>
    <!-- 内容循环 -->
    <div v-else>
      <div class="contentList-box" v-for="items in items">
        <div class="pull-left contentList-title">
          <h2>{{items.contentTitle}}</h2>
          <p>{{items.contentTime}}</p>
        </div>
        <img class="pull-right contentList-img" :src="items.contentImg" alt=""/>
        <div class="contentList-footer clearfix" style="clear:both">
          <span class="pull-left">{{items.contentType}}</span>
          <span class="pull-right">{{items.contentDesc}}</span>
        </div>
      </div>
    </div>
    {{contentListShow}}
  </div>
</template>

<script>
export default {
  data:function(){
    return {
      contentShow: true,
      items:[
        {
          contentTitle: "teamLab：花舞森林与未来游乐园",
          contentTime: "时间：2017年5月20日－2017年11月19日",
          contentImg: require("../assets/image/462df4bd746c32b.jpg"),
          contentType: "展览",
          contentDesc: "本活动来自话剧"
        },
        {
          contentTitle: "伊藤润二经典美学AR体验展",
          contentTime: "购买须知：电子票：现场扫码验票或凭姓名手机号入场（以现场为准",
          contentImg: require("../assets/image/131875877d7f285.jpg"),
          contentType: "展览",
          contentDesc: ""
        },
        {
          contentTitle: "秦汉文明展",
          contentTime: "展期：2017年9月17日开幕（展期以国博官网为准）地点：南",
          contentImg: require("../assets/image/e74003452677a41.jpg"),
          contentType: "展览",
          contentDesc: ""
        },
        {
          contentTitle: "赖声川剧场史诗 《如梦之梦》北京保利",
          contentTime: "话剧《如梦之梦》是21世纪初期华人剧场最受瞩目的话剧之一，是",
          contentImg: require("../assets/image/2efa2c1d1c5f87d.jpg"),
          contentType: "戏剧",
          contentDesc: "本活动来自栏目 话剧"
        },
        {
          contentTitle: "赵孟頫书画特展",
          contentTime: "赵孟頫书画特展9月5日 武英殿展厅更多信息，有待故宫官网发布",
          contentImg: require("../assets/image/8d7b1d60b3141e8.jpg"),
          contentType: "展览",
          contentDesc: ""
        },
        {
          contentTitle: "这样过周末心不累，周末去五台山寺院做义工，体验2日禅文化活动。",
          contentTime: "每周五晚北京的一群年轻人这样来五台山过周末－本心之旅_腾讯视",
          contentImg: require("../assets/image/eac04af8f200983.jpg"),
          contentType: "公益",
          contentDesc: ""
        }
      ]
    }
  },
  computed:{
    contentListShow:function (){
      var self = this;
      setTimeout(changeShow,3000);
      function changeShow(){
        self.contentShow = false;
      }
    }
  }

}
</script>

<style scoped>
#contentList {
  text-align: center;
}

.loading-content {
  margin-top: 25px;
  text-align: center;
}

.loading-content img {
  width: 3rem;
}

#contentList .contentList-box{
  padding: 30px 0;
  border-bottom:1px solid #e3e3e3;
}

#contentList .contentList-title {
  text-align: left;
  width: 70%;
}

#contentList .contentList-title h2 {
  color:#333;
  font-size: 1.6rem;
  font-weight: bold;
  margin-bottom: 10px;
}

#contentList .contentList-title p {
  font-size: 1.3rem;
  color:#aaa;
}

#contentList .contentList-img {
  width: 25%;
}

#contentList .contentList-footer {
  padding-top: 15px;
  font-size: 1.2rem;
  color:#aaa;
}

</style>



