<template>
  <div> 
    <link-list></link-list>
    <content-list></content-list>
  </div>
</template>

<script>
import linkList from "./linkList"
import contentList from "./contentList"
export default {
	mounted: function(){
		this.$http.get("/news/index").then(function (response) {
	        if(response.status === 200){
	          console.log(response.data);
	        }
	      })
	      .catch(function (error) {
	        console.log(error);
	      });
	},
  	components: {
    	'content-list': contentList,
    	'link-list': linkList
  	}
}
</script>

<style>
</style>



